.content {
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
}
.home .banner {
    position: relative;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 100vh;
    width: 100%;
    color: #fff;
}
.home .banner1 {
    background-image: url('../assets/images/home-banner-mob.jpg');
}
.home .banner2 {
    background-image: url('../assets/images/home-banner2-mob.jpg');
}
.home .banner::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.25;
}
.banner2 .content {
    padding: 40px 16px;
}
.banner2 .content h2 {
    font-size: 32px;
    text-align: center;
    margin-bottom: 32px;
}
.message {
    max-width: 768px;
    margin: 0 auto;
}
.message p {
    font-weight: 100;
    margin: 24px 0;
    font-size: 18px;
}
.watermark svg path {
    fill: #fff;
}
.watermark svg {
    width: 160px;
    margin-bottom: 24px;
    margin: 14px auto;
    display: block;
}

.scroll svg {
    max-width: 28px;
    width: 100%;
}
.scroll {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    padding-bottom: 48px;
    cursor: pointer;
    -webkit-animation-iteration-count: 5;
    animation-iteration-count: 5;
    animation-delay: 3.5s;
}
.signature {
    position: relative;
    align-self: center;
    font-size: 40px;
    font-family: 'salesforce';
    font-weight: 100;
    color: #fff;
    margin: 40px;
    padding-bottom: 50%;
    transition: 200ms all ease-in-out;
    opacity: 0.8;
}
.signature svg {
    width: 100%;
    height: 100%;
}

@media only screen and (min-width: 48em) {
    .banner2 .content {
        padding: 60px 24px;
    }
    .watermark svg {
        width: 196px;
    }
    .signature {
        margin: 80px;
        font-size: 64px;
        padding-bottom: 20%;
    }
    .message p {
        font-size: 22px;
    }
    .banner2 .content h2 {
        font-size: 40px;
    }
    .home .banner1 {
        background-position: center 80%;
    }
    .home .banner2 {
        background-position: 70% center;
    }
}

@media only screen and (min-width: 78rem) {
    .home .banner1 {
        background-image: url('../assets/images/home-banner.jpg');
        background-position: center;
    }
    .home .banner2 {
        background-image: url('../assets/images/home-banner2.jpg');
        background-position: 70% center;
    }
    .banner2 .content {
        padding: 80px 0;
    }
    .banner2 .content h2 {
        font-size: 48px;
    }
    .watermark svg {
        width: 248px;
    }
    .message p {
        font-size: 32px;
    }
    .signature {
        padding-bottom: 5%;
    }
}